import {map, takeUntil} from 'rxjs/operators';
import {Component, OnInit} from '@angular/core';
import {OrderStatus} from 'src/app/core/enums/OrderStatus.enum';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';
import {OrderResponse} from 'src/app/core/models/seller/order/response/OrderResponse';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {SellerOrderService} from 'src/app/core/services/seller/seller-order/seller-order.service';
import {SpinnerService} from 'src/app/core/services/comon-services/spinner/spinner.service';
import {Observable} from 'rxjs';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {OrderAndOrderDetailResponse} from 'src/app/core/models/seller/order/response/OrderAndOrderDetailResponse';

@Component({
	selector: 'app-seller-pre-order-inprogress',
	templateUrl: './seller-pre-order-inprogress.component.html',
	styleUrls: ['./seller-pre-order-inprogress.component.less']
})
export class SellerPreOrderInprogressComponent implements OnInit {
	isLoading: boolean = true;
	isContinueLoading = false;
	tabIndexOpening = 0;
	orderId?: number;
	pageIndex: number;
	pageResult: PagedResult<OrderResponse>;
	orderResponses: Array<OrderResponse>;
	orderDetails$?: Observable<ApiResult<OrderAndOrderDetailResponse>>;
	constructor(
		private sellerOrderService: SellerOrderService,
		private destroy$: DestroyService,
		private notificationService: NotificationService,
		private sellerSpinnerService: SpinnerService
	) {
		this.pageIndex = 1;
		this.orderResponses = [];
		this.pageResult = {} as PagedResult<OrderResponse>;
	}
	ngOnInit(): void {
		this.isLoading = true;
		this.getData(1, OrderStatus.inProgressPreOrder);
	}
	getData(pageIndex: number, orderStatus) {
		this.sellerOrderService
			.getOrders({
				orderRequestType: orderStatus,
				pageIndex: pageIndex,
				pageSize: 10
			})
			.pipe(
				takeUntil(this.destroy$),
				map((value) => {
					if (value.resultObj) {
						value.resultObj.items.map((order) => {
							order.status = 'Cần Duyệt Đặt Hàng';
							return order;
						});
						return value;
					} else {
						return value;
					}
				})
			)
			.subscribe((result) => {
				console.log(result);
				if (result) {
					if (result.resultObj) {
						if (result.isSuccessed) {
							this.pageResult = result.resultObj;
							this.orderResponses = result.resultObj.items;
							this.isLoading = false;
							this.isContinueLoading = false;
							console.log(this.pageResult);
							//this.sellerSpinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
						} else {
						}
					} else {
					}
				} else {
				}
			});
	}
	deleteOrder(orderId: number) {
		this.sellerSpinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		this.sellerOrderService
			.postDeleteOrder(orderId)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						if (result.isSuccessed) {
							this.deleteProductFromView();
							if (result.message) {
								this.notificationService.showSuccessNotification(result.message);
							} else {
							}
						} else {
							if (result.message) {
								this.notificationService.showErrorNotification(result.message);
							} else {
							}
						}
					} else {
					}
					this.sellerSpinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
				},
				() => {
					this.notificationService.showErrorNotification('error');
					this.sellerSpinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
				}
			);
		console.log(orderId);
	}
	confirmOrder(orderId: number) {
		this.sellerSpinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		if (this.tabIndexOpening == 0) {
			this.sellerOrderService
				.postConfirmOrder(orderId, OrderStatus.inProgressPreOrder)
				.pipe(takeUntil(this.destroy$))
				.subscribe((result) => {
					if (result) {
						if (result.isSuccessed) {
							this.deleteProductFromView();
							if (result.message) {
								this.notificationService.showSuccessNotification(result.message);
							} else {
							}
						} else {
							if (result.message) {
								this.notificationService.showErrorNotification(result.message);
							} else {
							}
						}
					} else {
					}
					this.sellerSpinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
				});
		}
		if (this.tabIndexOpening == 1) {
			this.sellerOrderService
				.postConfirmOrder(orderId, OrderStatus.confirmPreOrder)
				.pipe(takeUntil(this.destroy$))
				.subscribe((result) => {
					if (result) {
						if (result.isSuccessed) {
							this.deleteProductFromView();
							if (result.message) {
								this.notificationService.showSuccessNotification(result.message);
							} else {
							}
						} else {
							if (result.message) {
								this.notificationService.showErrorNotification(result.message);
							} else {
							}
						}
					} else {
					}
					this.sellerSpinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
				});
		}

		console.log(orderId);
	}
	deleteProductFromView() {
		this.orderResponses = [];
		this.pageIndex = 1;
		if (this.tabIndexOpening == 0) {
			this.getData(this.pageIndex, OrderStatus.inProgressPreOrder);
		}
		if (this.tabIndexOpening == 1) {
			this.getData(this.pageIndex, OrderStatus.confirmPreOrder);
		}
	}
	cancel() {
		return;
	}
	showModal(order: OrderResponse) {
		order.isOrderDetailVisible = true;
		this.orderDetails$ = this.sellerOrderService.getOrderDetail(order.orderId);
	}
	handleCancel(order: OrderResponse) {
		order.isOrderDetailVisible = false;
	}
	handleOk(order: OrderResponse) {
		order.isOrderDetailVisible = false;
	}
	pageChanged(page: number) {
		this.pageIndex = page;
		if (this.tabIndexOpening == 0) {
			this.getData(this.pageIndex, OrderStatus.inProgressPreOrder);
		}
		if (this.tabIndexOpening == 1) {
			this.getData(this.pageIndex, OrderStatus.confirmPreOrder);
		}
	}
	ChangeTab(tabIndex: number) {
		console.log(tabIndex);
		this.pageResult = {} as PagedResult<OrderResponse>;
		this.isLoading = true;
		this.tabIndexOpening = tabIndex;
		this.pageIndex = 1;
		if (this.tabIndexOpening == 0) {
			this.getData(this.pageIndex, OrderStatus.inProgressPreOrder);
		}
		if (this.tabIndexOpening == 1) {
			this.getData(this.pageIndex, OrderStatus.confirmPreOrder);
		}
	}
}
